
body{
  /* background-image: url('./source/background.png') */
  /* background-image: linear-gradient(#473253, #243e5f); */
  background-color: #252627;
}
a{
  text-decoration: none;
}
li{
  list-style: none;
}
ul{
  margin: 0;
  padding: 0;
}
div{
  margin: 0;
  padding: 0;
}

.wrap{
  display: flex;
}

.left-menu{
  display: flex;
  width: 50px;
  min-height: 100vh;
  background-color: #181818;
  flex-direction: column;
  justify-content: space-between;
}

.left-menu_header{
  text-align: center;
  padding: 10px 0;
}
.left-menu_logo{
  color: #08fdd8;
  font-weight: bolder;
  font-size: 48px;
  text-shadow:2px 0px 3px #eee;
  transition: all .25 ease-out;
  margin: 0;
}
.left-menu_logo:hover{
  animation: logoAnimation 0.8s ease-in-out  1;
}
.left-menu_logo__desc{
  color: #fff;
  font-size: 12px;
}

.left-menu_content{
  justify-content: flex-start;

}
.left-menu_content__elem{
  text-align: center;
  color: #4d4d4e;
  font-size: 25px;

}
.left-menu_content__elem_active{
  text-align: center;
  color: #179180;
  font-size: 25px;
}
.navLink-active-span{
  font-size: 11px;
  color: #08fdd8;
  position: absolute;
  text-align: center;
  display: block;
  margin-top: -24px;
  width: 50px;
  opacity: 0;
}
.left-menu_footer{
    text-align: center;
    padding-bottom: 10px;
}
.social-link{
  color: #4d4d4d;
  font-size: 16px;
}
.social-link:hover{
  color: #179180;
}


@keyframes logoAnimation {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    -ms-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1)
    }
    
    10%,
    20% {
    -webkit-transform: scale3d(.9, .9, .9) rotate3d(0, 0, 1, -3deg);
    -ms-transform: scale3d(.9, .9, .9) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(.9, .9, .9) rotate3d(0, 0, 1, -3deg)
    }
    
    30%,
    50%,
    70%,
    90% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    -ms-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg)
    }
    
    40%,
    60%,
    80% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    -ms-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg)
    }
    
    100% {
    -webkit-transform: scale3d(1, 1, 1);
    -ms-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1)
    }
}

@media screen and (max-width: 670px){
  .left-menu{
    display: flex;
    width: 100%;
    min-height: 50px;
    background-color: #181818;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    top: 0;
  }
  .left-menu_content{
    display: flex;
    width: 80%;
    justify-content: center;
    align-items: center;
  }
  .left-menu_content ul{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
  }
  .left-menu_content__elem{
    font-size: 16px;
    text-align: start;

  }
  .left-menu_content span{
    display: inline;
  }
  .left-menu_content__elem_active{
    font-size: 16px;
    text-align: start;
    margin: 0;
    padding: 0;
  }
  .navLink-active-span{
    font-size: 11px;
    color: #08fdd8;
    position: relative;
    left: -5%;
    top: -24px;
    opacity: 0;
    width: auto;
  }
}