.homeWrapper{
  margin: 25% 50px;
}
.homeDescription{
    color: #8d8d8d;
}
.standardButton{
    border: 1px solid #08fdd8;
    background-color: #fff0;
    padding: 8px 16px;
    border-radius: 3px;
    color: #08fdd8; 
    cursor: pointer;
    -webkit-transition: bac 0.3 ease-in-out;
    transition: bac 0.3 ease-in-out;
    font-size: 14px;
    margin-top: 5%;
    font-weight: bold;
    opacity: 0;
    -webkit-animation: buttonAnimation 0.6s ease-out  1 forwards;
            animation: buttonAnimation 0.6s ease-out  1 forwards;
}
.standardButton:hover{
    color: #252627;
    background-color: #08fdd8;
}

@-webkit-keyframes buttonAnimation {
    0%{
        opacity: 0;
        -webkit-transform: translateY(-10px) rotate(30deg);
                transform: translateY(-10px) rotate(30deg);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
    }
}

@keyframes buttonAnimation {
    0%{
        opacity: 0;
        -webkit-transform: translateY(-10px) rotate(30deg);
                transform: translateY(-10px) rotate(30deg);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0) rotate(0deg);
                transform: translateY(0) rotate(0deg);
    }
}
.aboutBox{
    padding: 20% 0 0 10%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;

}
.aboutTitle{
    color: #08fdd8;
    font-size: 50px;
    display: inline-block;
    font-weight: bolder;
    opacity: 0;
    -webkit-animation: aboutTextAnimation 0.5s  ease-out  1 forwards;
            animation: aboutTextAnimation 0.5s  ease-out  1 forwards;
}
.aboutText, .sectionTitle, .aboutTextLight{
    color: azure;
    font-family: Open Sans, sans-serif;
    font-weight: 400;
    width: 100%;
    opacity: 0;
    -webkit-animation: aboutTextAnimation 0.5s 0.1s ease-out  1 forwards;
            animation: aboutTextAnimation 0.5s 0.1s ease-out  1 forwards;
}
.aboutTextLight{
    font-weight: 300;
}
.sectionTitle{
    color: #08fdd8;
    font-weight: 500;
  
}
@-webkit-keyframes aboutTextAnimation {
    0%{
        opacity: 0;
        -webkit-transform: translateY(20px);
                transform: translateY(20px)
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
}
@keyframes aboutTextAnimation {
    0%{
        opacity: 0;
        -webkit-transform: translateY(20px);
                transform: translateY(20px)
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0)
    }
}
.alertBox{
    position: absolute;
    top: 0;
    width: 80%;
    min-height: 50px;
    max-width: 510px;
    min-width: 250px;
    opacity: 0;
    -webkit-animation: alertBoxAn 0.8s ease-out  1 forwards;
            animation: alertBoxAn 0.8s ease-out  1 forwards;
    border-radius: 3px;
}
.alert-error{
    background-color: brown;  
}

.alert-success{
    background-color: #60d376;
}

.alert-waiting{
    background-color: #141818;
}
.alert-top{
    top: 0;
}
.alert-bottom{
    bottom: 0;
}

 
.alert-success-icon, .alert-text, .alert-close, .alert-error-icon, .alert-waiting-icon{
    color: #fff;
    font-size: 20px;
}
.alert-close{
    cursor: pointer;
}
.alert-content{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 12.5px 0 0 12.5px;
    
}
@-webkit-keyframes alertBoxAn {
    0%{
        position: absolute;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0;
    }
    100%{
        position: absolute;
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
        opacity: 1; 
    }
}
@keyframes alertBoxAn {
    0%{
        position: absolute;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        opacity: 0;
    }
    100%{
        position: absolute;
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
        opacity: 1; 
    }
}


